import { Component, OnInit, ViewChild } from '@angular/core';
import { data } from './data';

@Component({
  selector: 'my-app',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  trendsShown : boolean = false;

  historyXaxis = { categories: ['2018-Q1', '2018-Q2', '2018-Q3', '2018-Q4', '2019-Q1', '2019-Q2'] };
  trendsXaxis = { categories: ['2018-Q2', '2018-Q3', '2018-Q4', '2019-Q1', '2019-Q2'] };

  historyYaxis = {
    labels: {
      formatter: function (value) {
        const locale = 'he-IL';
        const options = {};
        return Intl.NumberFormat(locale, options).format(value);;
      }
    },
  };

  trendsYaxis = {
    labels: {
      formatter: function (value) {
        const locale = 'he-IL';
        const options = {};
        return Intl.NumberFormat(locale, options).format(value) + "%";
      }
    },
  };

  history = [
    {
      name: "test",
      data: [1, 2, 3]
    }
  ];
  trends = [];

  grid = {
    row: {
      colors: ['#f3f3f3', 'transparent'],
      opacity: 0.5
    }
  };

  chart = {
    height: 300,
    type: "line",
    zoom: {
      enabled: false
    },
    shadow: {
      enabled: true,
      color: '#000',
      top: 18,
      left: 7,
      blur: 10,
      opacity: 1
    },
    toolbar: {
      show: false
    }
  };

  constructor() { }

  ngOnInit() {

    console.log(data);
  }



  items = data;
  /*[
    {
      dimensions: ["", ""],
      cells: ["", "", "", "", ""]
    },
    {
      dimensions: ["הכנסות ריבית", "1"],
      cells: [{
        text: "1,002",
        data: [500, 850, 800, 900, 750, 1002]
      },
      {
        text: "5,002",
        data: [4300, 4200, 4400, 4300, 4500, 5002]
      }]
    },
    {
      dimensions: ["הוצאות ריבית", "2"],
      category: "test2",
      cells: [{ text: "1,000" }, { text: "9,000" }]
    }
  ];*/

  selectedCell: any;
  selectedItem : any;
  title: any = { text: '' };
  cellClick(item: any, cell: any) {
    this.selectedCell = cell;
    this.history = [{ name: "", data: cell.history }];
    this.trends = cell.trend ;
    this.selectedItem = item;
    //this.title = { text: item.dimensions[0], align: 'right' };
  }


  showTrends() {
    this.trendsShown = true;
  }
}
